/* From Uiverse.io by Galahhad */
.theme-switch {
    --theme-toggle-size: 9.6px;
    // --theme-toggle-size: 12.8px;
    // --theme-toggle-size: 30px;
    /* the size is adjusted using font-size,
       this is not transform scale,
       so you can choose any size */
    --theme-toggle-container-width: 5.625em;
    --theme-toggle-container-height: 2.5em;
    --theme-toggle-container-radius: 6.25em;
    /* radius 0 - minecraft mode :) */
    --theme-toggle-container-light-bg: #3D7EAE;
    --theme-toggle-container-night-bg: #1D1F2C;
    --theme-toggle-circle-container-diameter: 3.375em;
    --theme-toggle-sun-moon-diameter: 2.125em;
    --theme-toggle-sun-bg: #ECCA2F;
    --theme-toggle-moon-bg: #C4C9D1;
    --theme-toggle-spot-color: #959DB1;
    --theme-toggle-circle-container-offset: calc((var(--theme-toggle-circle-container-diameter) - var(--theme-toggle-container-height)) / 2.3 * -1);
    // --theme-toggle-circle-container-offset: calc((var(--theme-toggle-circle-container-diameter) - var(--theme-toggle-container-height)) / 2 * -1);
    --theme-toggle-stars-color: #fff;
    --theme-toggle-clouds-color: #F3FDFF;
    --theme-toggle-back-clouds-color: #AACADF;
    --theme-toggle-transition: .5s cubic-bezier(0, -0.02, 0.4, 1.25);
    --theme-toggle-circle-transition: .3s cubic-bezier(0, -0.02, 0.35, 1.17);
}

.theme-switch,
.theme-switch *,
.theme-switch *::before,
.theme-switch *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-size: var(--theme-toggle-size);
}

.theme-switch__container {
    width: var(--theme-toggle-container-width);
    height: var(--theme-toggle-container-height);
    background-color: var(--theme-toggle-container-light-bg);
    border-radius: var(--theme-toggle-container-radius);
    overflow: hidden;
    cursor: pointer;
    box-shadow: 0em -0.062em 0.062em rgba(0, 0, 0, 0.25), 0em 0.062em 0.125em rgba(255, 255, 255, 0.94);
    transition: var(--theme-toggle-transition);
    position: relative;
}

.theme-switch__container::before {
    content: "";
    position: absolute;
    z-index: 1;
    inset: 0;
    box-shadow: 0em 0.05em 0.187em rgba(0, 0, 0, 0.25) inset, 0em 0.05em 0.187em rgba(0, 0, 0, 0.25) inset;
    border-radius: var(--theme-toggle-container-radius)
}

.theme-switch__checkbox {
    display: none;
}

.theme-switch__circle-container {
    width: var(--theme-toggle-circle-container-diameter);
    height: var(--theme-toggle-circle-container-diameter);
    background-color: rgba(255, 255, 255, 0.1);
    position: absolute;
    left: var(--theme-toggle-circle-container-offset);
    top: var(--theme-toggle-circle-container-offset);
    border-radius: var(--theme-toggle-container-radius);
    box-shadow: inset 0 0 0 3.375em rgba(255, 255, 255, 0.1), inset 0 0 0 3.375em rgba(255, 255, 255, 0.1), 0 0 0 0.625em rgba(255, 255, 255, 0.1), 0 0 0 1.25em rgba(255, 255, 255, 0.1);
    display: -ms-flexbox;
    display: flex;
    transition: var(--theme-toggle-circle-transition);
    pointer-events: none;
}

.theme-switch__sun-moon-container {
    pointer-events: auto;
    position: relative;
    z-index: 2;
    width: var(--theme-toggle-sun-moon-diameter);
    height: var(--theme-toggle-sun-moon-diameter);
    margin: auto;
    border-radius: var(--theme-toggle-container-radius);
    background-color: var(--theme-toggle-sun-bg);
    box-shadow: 0.062em 0.062em 0.062em 0em rgba(254, 255, 239, 0.61) inset, 0em -0.062em 0.062em 0em #a1872a inset;
    filter: drop-shadow(0.062em 0.125em 0.125em rgba(0, 0, 0, 0.25)) drop-shadow(0em 0.062em 0.125em rgba(0, 0, 0, 0.25));
    overflow: hidden;
    transition: var(--theme-toggle-transition);
}

.theme-switch__moon {
    -ms-transform: translateX(100%);
    transform: translateX(100%);
    width: 100%;
    height: 100%;
    background-color: var(--theme-toggle-moon-bg);
    border-radius: inherit;
    box-shadow: 0.062em 0.062em 0.062em 0em rgba(254, 255, 239, 0.61) inset, 0em -0.062em 0.062em 0em #969696 inset;
    transition: var(--theme-toggle-transition);
    position: relative;
}

.theme-switch__spot {
    position: absolute;
    top: 0.75em;
    left: 0.312em;
    width: 0.75em;
    height: 0.75em;
    border-radius: var(--theme-toggle-container-radius);
    background-color: var(--theme-toggle-spot-color);
    box-shadow: 0em 0.0312em 0.062em rgba(0, 0, 0, 0.25) inset;
}

.theme-switch__spot:nth-of-type(2) {
    width: 0.375em;
    height: 0.375em;
    top: 0.937em;
    left: 1.375em;
}

.theme-switch__spot:nth-last-of-type(3) {
    width: 0.25em;
    height: 0.25em;
    top: 0.312em;
    left: 0.812em;
}

.theme-switch__clouds {
    width: 1.25em;
    height: 1.25em;
    background-color: var(--theme-toggle-clouds-color);
    border-radius: var(--theme-toggle-container-radius);
    position: absolute;
    bottom: -0.625em;
    left: 0.312em;
    box-shadow: 0.937em 0.312em var(--theme-toggle-clouds-color), -0.312em -0.312em var(--theme-toggle-back-clouds-color), 1.437em 0.375em var(--theme-toggle-clouds-color), 0.5em -0.125em var(--theme-toggle-back-clouds-color), 2.187em 0 var(--theme-toggle-clouds-color), 1.25em -0.062em var(--theme-toggle-back-clouds-color), 2.937em 0.312em var(--theme-toggle-clouds-color), 2em -0.312em var(--theme-toggle-back-clouds-color), 3.625em -0.062em var(--theme-toggle-clouds-color), 2.625em 0em var(--theme-toggle-back-clouds-color), 4.5em -0.312em var(--theme-toggle-clouds-color), 3.375em -0.437em var(--theme-toggle-back-clouds-color), 4.625em -1.75em 0 0.437em var(--theme-toggle-clouds-color), 4em -0.625em var(--theme-toggle-back-clouds-color), 4.125em -2.125em 0 0.437em var(--theme-toggle-back-clouds-color);
    transition: 0.5s cubic-bezier(0, -0.02, 0.4, 1.25);
}

.theme-switch__stars-container {
    position: absolute;
    color: var(--theme-toggle-stars-color);
    top: -100%;
    left: 0.312em;
    width: 2.75em;
    height: auto;
    transition: var(--theme-toggle-transition);
}

/* actions */

.theme-switch__checkbox:checked+.theme-switch__container {
    background-color: var(--theme-toggle-container-night-bg);
    border: 1px solid var(--border-strong);
}

.theme-switch__checkbox:checked+.theme-switch__container .theme-switch__circle-container {
    // left: calc(100% - var(--theme-toggle-circle-container-offset) - var(--theme-toggle-circle-container-diameter));
    left: calc(100% - var(--theme-toggle-circle-container-offset) - var(--theme-toggle-circle-container-diameter) + .13em);
    top: calc(var(--theme-toggle-circle-container-offset) - .13em); // Add more
}

.theme-switch__checkbox:checked+.theme-switch__container .theme-switch__circle-container:hover {
    left: calc(100% - var(--theme-toggle-circle-container-offset) - var(--theme-toggle-circle-container-diameter) - 0.187em)
}

.theme-switch__circle-container:hover {
    left: calc(var(--theme-toggle-circle-container-offset) + 0.187em);
}

.theme-switch__checkbox:checked+.theme-switch__container .theme-switch__moon {
    transform: translate(0);
}

.theme-switch__checkbox:checked+.theme-switch__container .theme-switch__clouds {
    bottom: -4.062em;
}

.theme-switch__checkbox:checked+.theme-switch__container .theme-switch__stars-container {
    top: 50%;
    transform: translateY(-50%);
}