    // Social -----------------------------------------------------------
    --twitter: 						#{$twitter};
    --facebook: 					#{$facebook};
    --google: 						#{$google};
    --dribbble: 					#{$dribbble};
    --behance: 						#{$behance};
    --linkedin: 					#{$linkedin};
    --pinterest: 					#{$pinterest};
    --youtube: 					    #{$youtube};
    --tumblr: 					    #{$tumblr};
    --github-alt: 					#{$github-alt};
    --reddit: 					    #{$reddit};
    --skype: 					    #{$skype};

    



    
    // Typography -------------------------------------------------------

    // Plus Jakarta Sans, Space Grotesk, Gilroy
    --font-family:                  'Space Grotesk', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    --font-family-code:             ui-monospace, Menlo, Consolas, Monaco, monospace;
    --font-size-base:               var(--body-font-size-md);
    
    --font-weight-regular:          400;
    --font-weight-medium:           500;
    --font-weight-semibold:         600;
    --font-weight-bold:             700;

    // Body
    --body-font-size-sm:            12px;
    --body-font-size-md:            14px;
    --body-font-size-lg:            16px;
    
    --body-line-height-sm:          16px;
    --body-line-height-md:          20px;
    --body-line-height-lg:          24px;


    // Display
    --display-1:                    96px;
    --display-2:                    64px;
    --display-3:                    48px;

    --display-font-weight-1:        var(--font-weight-bold);   // Bold
    --display-font-weight-2:        var(--font-weight-bold);
    --display-font-weight-3:        var(--font-weight-bold);

    --display-line-height-1:        144px;
    --display-line-height-2:        80px;
    --display-line-height-3:        72px;


    // Heading
    --heading-1:                    40px;
    --heading-2:                    32px;
    --heading-3:                    28px;
    --heading-4:                    24px;
    --heading-5:                    20px;
    --heading-6:                    18px;

    --heading-font-weight-1:        var(--font-weight-bold);   // Bold
    --heading-font-weight-2:        var(--font-weight-bold);
    --heading-font-weight-3:        var(--font-weight-bold);
    --heading-font-weight-4:        var(--font-weight-semibold);   // Semibold
    --heading-font-weight-5:        var(--font-weight-semibold);
    --heading-font-weight-6:        var(--font-weight-semibold);

    --heading-line-height-1:        60px;
    --heading-line-height-2:        48px;
    --heading-line-height-3:        42px;
    --heading-line-height-4:        36px;
    --heading-line-height-5:        30px;
    --heading-line-height-6:        26px;

    




    // Spacing ---------------------------------------------------------
    --space-xs-4:                   2px;
    --space-xs-3:                   4px;
    --space-xs-2:                   8px;
    --space-xs:                     12px;
    --space-sm:                     16px;
    --space-md:                     24px;
    --space-lg:                     32px;
    --space-xl:                     40px;
    --space-xl-2:                   48px;
    --space-xl-3:                   56px;
    --space-xl-4:                   64px;
    --space-xl-5:                   72px;
    --space-xl-6:                   96px;
    --space-xl-7:                   128px;
    
    



    
    // Spacing ---------------------------------------------------------
    --radius-xs-4:                  2px;
    --radius-xs-3:                  4px;
    --radius-xs-2:                  8px;
    --radius-xs:                    12px;
    --radius-sm:                    16px;
    --radius-md:                    24px;
    --radius-lg:                    32px;
    --radius-xl:                    40px;
    --radius-rounded:               128px;
    
    



    
    // UI control ------------------------------------------------------
    --ui-size-xs-2:                 var(--space-sm);
    --ui-size-xs:                   var(--space-md);
    --ui-size-sm:                   var(--space-lg);
    --ui-size-md:                   var(--space-xl);
    --ui-size-lg:                   var(--space-xl-2);
    
    --ui-radius-sm:                 var(--radius-xs-2);
    --ui-radius-md:                 var(--radius-xs);
    --ui-radius-lg:                 var(--radius-sm);

    



    
    // Dimension Theme -------------------------------------------------
    --section-margin-mobile:        var(--space-xl-4);
    --section-margin-web:           var(--space-xl-7);
    --section-title-marign:         var(--space-lg);


    --border-angle: 0deg;


    --transition-base: ease-out;
    // --transition-base: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

@property --border-angle {
    syntax: "<angle>";
    initial-value: 0deg;
    inherits: false;
}